import { _decorator, Component, Node } from 'cc';




export class CardStruct
{
    constructor(_num : number , _type : CardType)
    {
        this.mNum = _num;
        this.mType = _type;
    }
    public mType : CardType;
    public mNum : number;
}

export enum CardType
{
    None,
    Diamond,
    Club,
    Heart,
    Speades
}

export enum Combiantion
{
    None,
    High,
    OnePair,
    TwoPair,
    ThreeOfKind,
    Straight,
    Flush,
    FullHouse,
    FourOfKind,
    StraightFlush,
    RoyalStraightFlush,
}

export class Calculator{
    private static instance : Calculator = null;
    public static GetInstance() :Calculator
    {
        if(Calculator.instance == null)
        {
            Calculator.instance = new Calculator();
        }
        return Calculator.instance;
    }


    public TryToCalculate( _publicCards:Array<CardStruct> , _playerCards:Array<CardStruct> ) : Array<CardStruct>
    {
        let tempPublicCards =  JSON.parse(JSON.stringify(_publicCards));
        let tempPlayerCards = JSON.parse(JSON.stringify(_playerCards));
        let totalCardList = tempPublicCards.concat(tempPlayerCards);
        this.LogCards(totalCardList);

        //List<CardStruct> targetList = CreateCardList(tempCards);
        //List<CardStruct> result = CalculateCombination(targetList);
        // return result;

        return null;
    }


    public LogCards(_targetCards : Array<CardStruct>)
    {
        console.log("当前牌组====");
        for(let i = 0 ; i < _targetCards.length ; i++)
        {
            let typeStr = "";
            switch(_targetCards[i].mType)
            {
                case CardType.Club:
                    typeStr = "♣️";
                break;
                case CardType.Diamond:
                    typeStr = "♦️";
                break;
                case CardType.Speades:
                    typeStr = "♠️";
                break;
                case CardType.Heart:
                    typeStr = "♥️";
                break;
            }
            console.log("==== "+ _targetCards[i].mNum + typeStr);
        }
    }

    
}

